.client{
    padding: 150px 0px 294px 0px;
    margin: 0 auto;
    @media (max-width: 1399px) {
        padding: 0px;
    }
    @media (min-width: 1400px) {
        width: 1214px;
    }
}

.client-list--1{
    display: grid;
    grid-template-columns: 98px 159px 80.5px 128.5px 148.3px;
    align-items: center;
    column-gap: 150px;
    margin-bottom: 60px;
    @media (max-width: 1399px) {
        display: flex;
        margin-top: 75px;
        flex-direction: column;
        margin-bottom: 0px;
    }
}

.client-list--2{
    display: grid;
    grid-template-columns: 108px 113px 126px 91.8px 114px;
    align-items: center;
    column-gap: 165px;
    @media (max-width: 1399px) {
        display: flex;
        flex-direction: column;
        margin-bottom: 0px;
    }
}

.client-list__item{
    @media (max-width: 1399px) {
        margin-bottom: 25px;
    }
}